* {
  box-sizing: border-box;
}

.App {
  text-align: center;
}
.addButton {
  float: right;
  height: 15vh;
  width: 15vh;
  display: inline-block;
}
.container__row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: #404040;
}
.taskView {
  background-color: #404040;
}

.container__col {
  width: 33vw;
}

.tasklist {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.task {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  max-width: 700px;
  min-height: 150px;
  margin-top: 10px;
  margin-bottom: 10px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.leftcol {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.leftcol > * {
  margin: 10px;
}

.rightcol > * {
  margin: 10px;
}

.createlist {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 10px;
}

.forminput {
  height: 27px;
}

.settings {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 10vh;
}

.sortedBy {
  color: white;
  font-size: 3vh;
  overflow: hidden;
}

.sortButton:hover {
  background-color: #b3b3b3;
}

.basicButton:hover {
  background-color: #b3b3b3;
}

.basicButton {
  border: none;
}

@media only screen and (max-width: 992px) {
  #searchInput {
    width: 100%;
    font-size: 3vh;
    height: 7.7vh;
  }
  .sortedBy {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: auto;
    align-items: center;
    gap: 5px;
  }
  .menu-trigger {
    padding: 2vh 3vh;
    font-size: 3vh;
  }

  .sortButton {
    background-color: white;
    border: none;
    float: right;
  }

  .sortButton > img {
    width: 7vh;
  }

  .sortedBy {
    background-color: #353535;
    color: white;
  }

  .searchBar {
    height: 5vh;
  }
  .basicButton {
    background-color: #353535;
    box-shadow: 5px 5px 8px #404040;
    font-size: 3vh;
    width: 12vh;
    height: 12vh;
    color: white;
  }

  .listInput {
    box-shadow: 5px 5px 8px #404040;
    width: 200px;
    height: 12vh;
    font-size: 3vh;
  }
}

@media only screen and (min-width: 992px) {
  #searchInput {
    max-width: 400px;
    width: 60%;
    font-size: 3vh;
    height: 7.7vh;
  }
  .sortedBy {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: auto;
    align-items: center;
    gap: 5px;
  }
  .menu-trigger {
    padding: 2vh 3vh;
    font-size: 3vh;
  }

  .sortButton {
    background-color: white;
    border: none;
    float: right;
  }

  .sortButton > img {
    width: 7vh;
  }

  .sortedBy {
    background-color: #353535;
    color: white;
  }

  .searchBar {
    height: 5vh;
  }
  .basicButton {
    background-color: #353535;
    box-shadow: 5px 5px 8px #404040;
    font-size: 3vh;
    width: 12vh;
    height: 12vh;
    color: white;
  }

  .listInput {
    box-shadow: 5px 5px 8px #404040;
    width: 200px;
    height: 12vh;
    font-size: 3vh;
  }
}
