.syle-body {
  width: auto;
  height: 100%;
  justify-content: center;
  background-color: #212121;
}
.syle-topbar {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  padding-left: 50px;
  padding-right: 50px;
  padding-top: 10px;
  padding-bottom: 5px;
  font-size: 20px;
  justify-items: center;
  background-color: rgba(0, 0, 0, 0.8);
}
.syle-task-buttons {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 20px;
}
.syle-taskRemoveButtons {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 20px;
}
.syle-listbody {
  min-height: 100%;
  width: auto;
  padding-bottom: 120px;
}
.syle-search-sort {
  padding-top: 5px;
  display: grid;
  grid-template-columns: 75% 25%;
}
.syle-footer {
  position: fixed;
  bottom: 0px;
  padding: 10px;
  height: 130px;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.8);
}
